.adyen-checkout__voucher-result {
  box-sizing: border-box;
  border-radius: 12px;
  text-align: center;
  position: relative;
}

.adyen-checkout__voucher-result__top,
.adyen-checkout__voucher-result__bottom {
  background: #fff;
  border: 1px solid #d4d9db;
}

.adyen-checkout__voucher-result__top {
  padding: 40px 0 24px;
  border-radius: 12px 12px 0 0;
  border-bottom: 0;
}

.adyen-checkout__voucher-result__bottom {
  border-top: 0;
  border-radius: 0 0 12px 12px;
}

.adyen-checkout__voucher-result__separator {
  background: #fff;
  position: relative;
  width: calc(100% - 14px);
  height: 13px;
  margin: 0 auto;
  display: flex;
  align-items: center;
}

.adyen-checkout__voucher-result__separator::before,
.adyen-checkout__voucher-result__separator::after {
  background-image: url("data:image/svg+xml;base64,");
}

.adyen-checkout__voucher-result__separator::before {
  background-position: 100%;
  background-repeat: no-repeat;
  content: "";
  left: -7px;
  top: 0;
  position: absolute;
  width: 7px;
  height: 13px;
}

.adyen-checkout__voucher-result__separator::after {
  background-position: 100%;
  background-repeat: no-repeat;
  content: "";
  right: -7px;
  top: 0;
  position: absolute;
  width: 7px;
  height: 13px;
  transform: rotate(-180deg);
}

.adyen-checkout__voucher-result__separator__inner {
  width: 100%;
  border-top: 1px solid #e6e9eb;
}

.adyen-checkout__voucher-result__image {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-bottom: 40px;
}

.adyen-checkout__link--voucher-result-instructions {
  display: inline-block;
}

.adyen-checkout__voucher-result__image__wrapper {
  display: block;
  position: relative;
  height: 48px;
  margin: 0 24px;
}

.adyen-checkout__voucher-result__image__wrapper::after {
  border: 1px solid rgba(0, 27, 43, 0.17);
  border-radius: 3px;
  content: "";
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.adyen-checkout__voucher-result__image__wrapper:nth-child(2)::before {
  border-left: 1px solid #d4d9db;
  content: "";
  height: 64px;
  left: -24.5px;
  position: absolute;
  top: -8px;
  width: 1px;
}

.adyen-checkout__voucher-result__image__brand,
.adyen-checkout__voucher-result__image__issuer {
  height: 48px;
  border-radius: 3px;
}

.adyen-checkout__voucher-result__introduction {
  line-height: 19px;
  font-size: 0.81em;
  text-align: center;
  color: #00112c;
  max-width: 400px;
  margin: 0 auto;
}

.adyen-checkout__voucher-result__amount {
  margin: 24px auto 0;
  font-size: 1em;
  color: #00112c;
  text-align: center;
  font-weight: bold;
}

.adyen-checkout__voucher-result__surcharge {
  font-size: 0.81em;
  line-height: 19px;
  text-align: center;
  color: #687282;
  display: block;
  font-weight: normal;
}

.adyen-checkout__voucher-result__code__label {
  position: absolute;
  display: block;
  font-weight: normal;
  right: 0;
  left: 0;
  width: auto;
  line-height: 19px;
  top: -2px;
  margin: 0 auto;
  user-select: none;
}

.adyen-checkout__voucher-result__code__label::before {
  content: "";
  position: absolute;
}

.adyen-checkout__voucher-result__code__label__text {
  font-size: 13px;
  color: #00112c;
  background: #fff;
  padding: 0 8px;
  letter-spacing: normal;
  line-height: 1;
}

.adyen-checkout__voucher-result__code__barcode {
  display: block;
  margin: 0 auto;
  margin-bottom: 8px;
  max-width: 100%;
  height: 56px;
  user-select: none;
}

.adyen-checkout__voucher-result__code {
  position: relative;
  font-size: 1.5em;
  text-align: center;
  letter-spacing: 1px;
  color: #00112c;
  border-width: 1px 0;
  padding: 16px 48px;
  display: inline-block;
  margin: 0 auto;
  width: 100%;
  font-weight: bold;
  user-select: all;
  word-break: break-word;
  line-height: 1.2;
}

.adyen-checkout__voucher-result__details {
  list-style: none;
  padding: 0;
  margin: -1px auto 0;
}

.adyen-checkout__voucher-result__details__item {
  display: flex;
  justify-content: space-between;
  font-size: 0.81em;
  color: #00112c;
  padding: 16px 24px;
  border-top: 1px solid #e6e9eb;
  word-break: break-word;
}

.adyen-checkout__voucher-result__details__item:last-child {
  margin-bottom: 0;
}

.adyen-checkout__voucher-result__details__label {
  max-width: 50%;
  text-align: left;
}

.adyen-checkout__voucher-result__details__value {
  max-width: 50%;
  text-align: right;
  font-weight: bold;
}

.adyen-checkout__voucher-result__actions {
  margin: 0 auto 32px;
  max-width: 100%;
  min-width: 200px;
  width: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  padding: 0;
}

.adyen-checkout__voucher-result__actions__item {
  margin: 0 4px;
}